<template>
  <div id="app">

    <div class="surface-overlay py-3 px-6 shadow-2 flex align-items-center justify-content-between relative lg:static">
      <img src="/custom_logo.PNG" alt="Image" height="50" class="mr-0 lg:mr-5">
      <a
          v-ripple
          class="cursor-pointer block text-700 mt-1 p-ripple"
          @click="isOpen = !isOpen"
      >
        <i class="pi pi-bars text-4xl"/>
      </a>
      <div :class="{ hidden: isOpen }"
           class="close_open_menu align-items-center flex-grow-1 justify-content-between lg:flex absolute lg:static w-full surface-overlay left-0 top-100 px-6 lg:px-0 z-4 shadow-2 lg:shadow-none">
        <ul class="list-none p-0 m-0 flex lg:align-items-center text-900 select-none flex-column lg:flex-row cursor-pointer">


            <li>
              <router-link to="/"
                           class="navigation-style flex px-0 lg:px-5 py-3 hover:text-blue-600 font-medium transition-colors transition-duration-150 p-ripple"
                           @click="isOpen = !isOpen">
                <span>Home</span>
              </router-link>
            </li>
            <li v-if="showAdminBoard">
              <router-link to="/admin"
                           class="navigation-style flex px-0 lg:px-5 py-3 hover:text-blue-600 font-medium transition-colors transition-duration-150 p-ripple"
                           @click="isOpen = !isOpen">Admin Board
              </router-link>
            </li>
            <li v-if="showModeratorBoard">
              <router-link to="/mod"
                           class="navigation-style flex px-0 lg:px-5 py-3 hover:text-blue-600 font-medium transition-colors transition-duration-150 p-ripple"
                           @click="isOpen = !isOpen">Platform
              </router-link>
            </li>
            <li>
              <router-link v-if="currentUser" to="/user"
                           class="navigation-style flex px-0 lg:px-5 py-3 hover:text-blue-600 font-medium transition-colors transition-duration-150 p-ripple"
                           @click="isOpen = !isOpen">User
              </router-link>
            </li>


          <div> <LocaleChanger/></div>
        </ul>
        <div
            class="flex justify-content-between lg:block border-top-1 lg:border-top-none surface-border py-3 lg:py-0 mt-3 lg:mt-0">
          <div v-if="!currentUser">
            <Button label="Login" @click="isOpen = !isOpen, this.$router.push('/login');" role="link"
                    class="p-button-text font-bold"/>
            <Button label="Register" @click="isOpen = !isOpen; this.$router.push('/register')" class="ml-3 font-bold"/>
          </div>

          <div v-if="currentUser">
            <Button :label="currentUser.username" @click="isOpen = !isOpen;this.$router.push('/profile')" role="link"
                    class="p-button-text font-bold"/>
            <Button label="Log out" @click="isOpen = !isOpen" href @click.prevent="logOut" class="ml-3 font-bold"/>
          </div>
        </div>
      </div>
    </div>

<!--    <div>-->
<!--      <div>-->
<!--        <a v-if="$i18n.locale !== 'de'" v-on:click="changeLanguage('de')">DE</a>-->
<!--        <strong v-if="$i18n.locale === 'de'">DE</strong>-->
<!--        &nbsp;|&nbsp;-->
<!--        <a v-if="$i18n.locale !== 'en'" v-on:click="changeLanguage('en')">EN</a>-->
<!--        <strong v-if="$i18n.locale === 'en'">EN</strong>-->
<!--      </div>-->
<!--    </div>-->

    <div class="surface-ground px-2 py-2 md:px-6 lg:px-8 align-items-center justify-content-center">
      <router-view/>
    </div>
  </div>
</template>

<script>
import LocaleChanger from "@/components/LocaleChanger.vue";

export default {
  components: {LocaleChanger},
  data() {
    return {
      isOpen: true,
      loading: false,
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    showAdminBoard() {
      if (this.currentUser && this.currentUser['roles']) {
        return this.currentUser['roles'].includes('ROLE_ADMIN');
      }

      return false;
    },
    showModeratorBoard() {
      if (this.currentUser && this.currentUser['roles']) {
        return this.currentUser['roles'].includes('ROLE_MODERATOR');
      }

      return false;
    }
  },
  methods: {
    logOut() {
      this.$store.dispatch('auth/logout');
      this.$router.push('/login');
    },
  },
};
</script>
<style>
body {
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
