<template>

  <div>
    <Dropdown v-model="selectedValue" :options="options" optionLabel="label" :placeholder="selectedLanguage.value" @change="changeLanguage(selectedValue)">
    </Dropdown>
  </div>

</template>

<script>
export default {
  data() {
    return {
      options: [
        {label: 'English', value: 'en', image:'https://icons.iconarchive.com/icons/wikipedia/flags/1024/SE-Sweden-Flag-icon.png'},
        {label: 'lietuvi≈≥', value: 'lt', image: 'https://icons.iconarchive.com/icons/wikipedia/flags/1024/SE-Sweden-Flag-icon.png'},
        {label: 'Germany', value: 'de', image: 'https://icons.iconarchive.com/icons/wikipedia/flags/1024/SE-Sweden-Flag-icon.png'},
      ],
      selectedValue: JSON.parse(localStorage.getItem("language")) || {label: 'English', value: 'en', image:'https://icons.iconarchive.com/icons/wikipedia/flags/1024/SE-Sweden-Flag-icon.png'},
      selectedLanguage: "",
    };
  },
  created() {
    this.selectedValue = JSON.parse(localStorage.getItem("language")) || {label: 'English', value: 'en', image:'https://icons.iconarchive.com/icons/wikipedia/flags/1024/SE-Sweden-Flag-icon.png'}
    this.$i18n.locale = this.selectedValue.value
  },
  watch: {
    selectedValue(val) {
      localStorage.setItem("language", JSON.stringify(val));
    }
  },

  methods: {
    changeLanguage(language) {
      this.selectedLanguage = language
      this.$i18n.locale = this.selectedLanguage.value
    },
  },
};
</script>
