<script>
export default {
  props: {
    header: {
      type: String,
      default: null,
    },
    code: null,
    recent: {
      type: Boolean,
      default: false,
    },
    free: {
      type: Boolean,
      default: false,
    },
    containerClass: null,
    previewStyle: null,
  },
  data() {
    return {
      BlockView: {
        PREVIEW: 0,
        CODE: 1,
      },
      blockView: 0,
    }
  },
  computed: {
    codeDisabled() {
      return process.env.NODE_ENV === 'production' ? !this.free : false
    },
  },
  methods: {
    activateView(event, blockView) {
      if (!this.codeDisabled)
        this.blockView = blockView

      event.preventDefault()
    },
    async copyCode(event) {
      if (!this.codeDisabled)
        await navigator.clipboard.writeText(this.code)

      event.preventDefault()
    },
  },
}
</script>

<template>
  <div class="block-section">
    <div class="block-header">
      <span class="block-title">
        <span>{{ header }}</span>
        <span v-if="free" class="badge-free">Free</span>
        <span v-if="recent" class="badge-new">New</span>
      </span>
      <div class="block-actions">
        <a tabindex="0" :class="{ 'block-action-active': blockView === BlockView.PREVIEW }" @click="activateView($event, BlockView.PREVIEW)"><span>Preview</span></a>
        <a :tabindex="codeDisabled ? null : '0'" :class="{ 'block-action-active': blockView === BlockView.CODE, 'block-action-disabled': codeDisabled }" @click="activateView($event, BlockView.CODE)">
          <i v-if="codeDisabled" class="pi pi-lock" />
          <span>Code</span>
        </a>
        <a
          v-tooltip.focus.bottom="{ value: 'Copied to clipboard', disabled: codeDisabled }" :tabindex="codeDisabled ? null : '0'" class="block-action-copy" :class="{ 'block-action-disabled': codeDisabled }"
          @click="copyCode($event)"
        ><i class="pi pi-copy" /></a>
      </div>
    </div>
    <div class="block-content">
      <div v-if="blockView == BlockView.PREVIEW" :class="containerClass" :style="previewStyle">
        <slot />
      </div>
      <div v-if="blockView === BlockView.CODE && !codeDisabled">
        <pre v-code><code>{{ code }}

</code></pre>
      </div>
    </div>
  </div>
</template>
